import CarService from '../services/CarService';

export default {
    state: {
        cars: []
    },
    getters: {
        carsToDisplay: state => state.cars
    },
    mutations: {
        setCars(state, { cars }) {
            state.cars = cars
        },
        addCar(state, {car}) {
            state.cars.unshift(car)
        }
    },
    actions: {
        loadCars(context) {
            return CarService.query()
                .then(cars => {
                    console.log({ cars })
                    context.commit({ type: 'setCars', cars })
                })
        },
        addCar(context, {car}) {
            return CarService.addCar(car)
                .then(car => {
                    context.commit({ type: 'addCar', car })
                })
        }
    }
}